.grid-35 {
  width: 100%;
  grid-template-columns: 492px 483px 483px;
  grid-template-rows: 84px 108px 233px 109px 117px 137px 108px;
  grid-gap: 7px;
  margin-top: 30px;

  .image-wrapper {
    &:nth-child(1) {
      grid-row: 1/6;
      grid-column: 1/2;
    }

    &:nth-child(2) {
      grid-row: 2/4;
      grid-column: 2/3;
    }

    &:nth-child(3) {
      grid-row: 4/7;
      grid-column: 2/3;
    }

    &:nth-child(4) {
      grid-row: 3/5;
      grid-column: 3/4;
    }

    &:nth-child(5) {
      grid-row: 5/8;
      grid-column: 3/4;
    }
  }
}