body {
  font-family: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  /* overflow: hidden; */
}

* {
  outline: none !important;
}

a {
  position: relative;
  display: block;
}
a::after {
  content: url("../img/lupa.png");
  position: absolute;
  right: 0;
  bottom: 0;
  width: 43px;
  height: 42px;
}

.image-wrapper img {
  width: 100%;
  height: 100%;
}

.d-grid a {
  height: 100%;
  width: 100%;
}

.page-content > .grid {
  display: grid;
}

.med {
  font-weight: 500;
}

.slider-container {
  padding: 2% 75px 10px;
  position: relative;
}
@media (max-width: 992px) {
  .slider-container {
    padding: 10px 20px;
  }
}

.museum-wrapper {
  position: relative;
}

.main-cols {
  display: flex;
  justify-content: space-between;
}

.swiper-button-disabled {
  display: none;
}

.logo-col {
  flex: 0 0 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main-col {
  flex: 0 0 72%;
}
.main-col h1 {
  text-transform: uppercase;
  color: #c39267;
  font-size: 50px;
  font-weight: 700;
}

.main-grid {
  display: grid;
  grid-template: 1fr 1fr/1fr 1fr;
  row-gap: 5px;
  column-gap: 10px;
}
.main-grid img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 197px;
}
@media (max-width: 992px) {
  .slider-button {
    width: 20px;
    height: 80px;
    z-index: 10;
  }
}

.museum-button-prev {
  left: 15px;
  background: url("../img/l.png") center/contain no-repeat;
}
@media (max-width: 992px) {
  .museum-button-prev {
    top: 160px;
    left: 160px;
  }
}

.museum-button-next {
  right: 15px;
  background: url("../img/r.png") center/contain no-repeat;
}
@media (max-width: 992px) {
  .museum-button-next {
    top: 100%;
    transform: initial;
    left: 20px;
  }
}

.page {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 992px) {
  .page {
    flex-wrap: wrap;
  }
}

.page-logo {
  flex: 0 0 15%;
  margin-right: 20px;
}
@media (max-width: 992px) {
  .page-logo {
    flex: 0 1 100%;
    display: flex;
  }
}
.page-logo h2 {
  text-transform: uppercase;
}
@media (max-width: 1400px) {
  .page-logo h2 {
    font-size: 24px;
  }
}
.page-logo img {
  max-width: 50%;
  height: auto;
  margin-bottom: 15px;
}

.page-content {
  flex: 0 1 80%;
}
@media (max-width: 992px) {
  .page-content {
    flex: 0 1 100%;
  }
}
.page-content p {
  font-size: 24px;
  line-height: 1.2;
  margin-bottom: 0;
}
@media (max-width: 1400px) {
  .page-content p {
    font-size: 20px;
  }
}
@media (max-width: 992px) {
  .page-content p {
    font-size: 18px;
  }
}

.small-text {
  font-size: 20px !important;
}
@media (max-width: 1400px) {
