    grid-column: 1/3 !important;
    grid-row: 1/2 !important;
  }
}

.grid-2 {
  display: grid;
  grid-template-columns: 2.35fr 3fr 1fr;
  grid-template-rows: repeat(auto-fit, minmax(200px, 1fr));
  column-gap: 2.5%;
  row-gap: 7px;
  align-items: center;
}
